@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
  font-weight: 500;
  font-style: medium;
  font-display: swap;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
img {
  display: block;
  object-fit: contain;
}
html {
  height: 100%;
}
button {
  padding: 0;
  font: inherit;
  border: none;
  cursor: pointer;
}
ul,
ol {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
a {
  display: block;
  color: inherit;
  text-decoration: none;
}
input {
  display: block;
  padding: 0;
  font: inherit;
  border: none;
  outline: none;
  appearance: textfield;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  display: none;
}
h1,
h2,
h3,
p,
dl,
dd {
  margin: 0;
}
fieldset,
textarea {
  border: 0;
  padding: 0;
  margin: 0;
}
textarea {
  resize: none;
}
legend {
  padding: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 769px) {
  .line-break--mob {
    display: none;
  }
}
@media (max-width: 768px), (min-width: 1440px) {
  .line-break--tab {
    display: none;
  }
}
@media (max-width: 768px) {
  .line-break--tab-ds {
    display: none;
  }
}
@media (min-width: 321px) {
  .line-break--only-mob {
    display: none;
  }
}
