.main-nav__toggle {
  position: absolute;
  right: 0;
  top: 11px;
  width: 24px;
  height: 16px;
  background-color: transparent;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
@media (min-width: 769px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__burger {
  display: block;
  position: absolute;
  left: 0;
  top: 11px;
  width: 24px;
  height: 1px;
  background-color: #000000;
}
.main-nav--closed .main-nav__burger::before,
.main-nav--closed .main-nav__burger::after {
  display: block;
  content: "";
  position: absolute;
  left: 0;
  width: 24px;
  height: 1px;
  background-color: #000000;
}
.main-nav--closed .main-nav__burger::before {
  transform: translateY(8px);
}
.main-nav--closed .main-nav__burger::after {
  transform: translateY(-8px);
}
@media (min-width: 769px) {
  .main-nav--closed .main-nav__burger {
    display: none;
  }
}
.main-nav--opened .main-nav__burger::before,
.main-nav--opened .main-nav__burger::after {
  display: block;
  content: "";
  position: absolute;
  left: 0;
  width: 24px;
  height: 1px;
  background-color: #000000;
}
.main-nav--opened .main-nav__burger::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__burger::after {
  transform: rotate(-45deg);
}
@media (min-width: 769px) {
  .main-nav--opened .main-nav__burger {
    display: none;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__list {
  position: static;
}
@media (max-width: 768px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
.main-nav--opened.main-nav--nojs .main-nav__list {
  position: static;
}
